import Vue from 'vue'
import Router from 'vue-router'

// Containers
const DefaultContainer = () => import('@/containers/DefaultContainer')

// Views
const Dashboard = () => import('@/views/Dashboard')

// Views - promo
const BonusV = () => import('@/views/promo/BonusV')
const Edit = () => import('@/views/promo/Edit')
const TurnOver = () => import('@/views/promo/TurnOver')

Vue.use(Router)

// export default new Router({
//   mode: 'hash',
//   linkActiveClass: 'open active',
//   scrollBehavior: () => ({ y: 0 }),
//   routes: [

//   ]
// })


/**
 * constantRoutes
 * 没有权限要求的基本页面
 * 可以访问所有角色
 */
export const constantRoutes = [
  {
    path: '/',
    redirect: '/dashboard',
    name: 'Home',
    component: DefaultContainer,
    children: [
      {
        path: 'dashboard',
        name: 'Dashboard',
        component: Dashboard,
      },
      {
        path: 'promo',
        name: 'Promo',
        component: {
          render(c) { return c('router-view') }
        },
        children: [
          {
            path: 'bonusv',
            name: 'BonusV',
            component: BonusV,
            meta: { title: 'BonusV', noCache: true }
          },
          {
            path: 'edit',
            name: 'Edit',
            component: Edit,
            meta: { title: 'Edit', noCache: true }
          },
          {
            path: 'turnover',
            name: 'TurnOver',
            component: TurnOver,
            meta: { title: 'TurnOver', noCache: true }
          }
        ]
      },
    ]
  },
]

/**
 * asyncRoutes
 * 需要根据用户角色动态加载的路由
 */
export const asyncRoutes = [
  // {
  //   path: '/permission',
  //   component: Layout,
  //   redirect: '/permission/page',
  //   name: 'Permission',
  //   meta: {
  //     title: 'Permission',
  //     icon: 'lock',
  //     roles: ['admin', 'editor']
  //   },
  //   children: []
  // },
  // // 必须将404页放置在最后！
  // { path: '*', redirect: '/404', hidden: true }
]

const createRouter = () => new Router({
  // mode: 'history', // require service support
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRoutes
})

const router = createRouter()

export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher // reset router
}

export default router